enum CellState {
  CLOSE = 'close',
  FLAGGED = 'flagged',
  QUESTION_MARKED = '?',
  OPEN = 'open',
  GHOST = 'ghost',
}

interface ICellInfo {
  readonly state: CellState;
  readonly hasMine: boolean;
  readonly mineCount: number;
}

class Cell {
  public state: CellState = CellState.CLOSE;

  constructor (
    readonly hasMine: boolean,
    readonly mineCount: number,
    state: CellState
  ) {
    this.hasMine = hasMine
    this.mineCount = mineCount
    this.state = state
  }
}

export {
  CellState,
  ICellInfo
}

export default Cell
