



























import { Component, Vue } from 'vue-property-decorator'
import MineBoard from '@/components/MineBoard.vue'
import ConfigBar from '@/components/ConfigBar.vue'
import ThanksFooter from '@/components/ThanksFooter.vue'
import Board, { IBoardSize } from '@/Board'
import MineBoardPreview from '@/components/MineBoardPreview.vue'
import ExplosionAnimation from '@/components/ExplosionAnimation.vue'

@Component({
  components: {
    ExplosionAnimation,
    MineBoard,
    MineBoardPreview,
    ConfigBar,
    ThanksFooter
  }
})
export default class App extends Vue {
  board?: Board | null = null;
  width: number = 0;
  height: number = 0;
  win: boolean = false;
  explosions: [number, number][] | null = null;

  onChangeBoardSize (size: IBoardSize) {
    console.log('onChangeBoardSize', size)
    this.width = size.width
    this.height = size.height
  }

  initBoard (size: IBoardSize) {
    console.log('initBoard(); size: ', size)
    this.win = false

    // reset board
    this.board = null
    this.$nextTick(() => { this.board = new Board(size) })
  }

  realizeGhostBoard (pos: number) {
    console.log('realizeGhostBoard')
    this.initBoard({ width: this.width, height: this.height })
  }

  onGameEnd (win: boolean, pos: number[]) {
    console.log(win)
    if (win) {
      this.win = true
    } else {
      this.explode(pos)
    }
  }

  explode (pos: number[]) {
    console.log('explode', pos)
    const mineBoard = this.$refs.mineBoard as MineBoard
    const offset = {
      x: window.pageXOffset,
      y: window.pageYOffset
    }
    this.explosions = pos
      .map(p => mineBoard.$el.children.item(p) as HTMLElement)
      .map(cell => {
        const rect = cell.getBoundingClientRect()
        return [
          offset.x + rect.left + rect.width / 2.0,
          offset.y + rect.top + rect.height / 2.0
        ]
      }) as [number, number][]
  }

  onAnimationEnd () {
    this.explosions = null
  }
}
