import Cell, { CellState, ICellInfo } from '@/Cell'
import { mines } from '@/config'
import { cross, shuffle, wrap2D } from '@/utils'

interface IBoardSize {
  width: number,
  height: number
}

type WrongCells = number[] | false

class Board {
  public readonly width: number;
  public readonly height: number;

  get cellInfos (): ICellInfo[] {
    return this.cells
  }

  private cells: Cell[];
  private opened = false;
  private justNowOpened: number[] = []
  private readonly neighbors8 = cross([-1, 0, 1], [-1, 0, 1])
    .filter(([x, y]) => x || y);

  constructor (size: IBoardSize) {
    this.width = size.width
    this.height = size.height
    this.cells = Array.from(
      { length: this.width * this.height },
      () => new Cell(false, 0, CellState.CLOSE)
    )
  }

  public open (pos: number): WrongCells {
    this.justNowOpened = []
    if (!this.opened) {
      this.opened = true
      this.cells = this.generate(pos)
    }

    this.openCells(true, ...this.coordFrom(pos))
    return this.wrongCells(this.justNowOpened)
  }

  public shouldOpenAround (pos: number): boolean {
    const [x, y] = this.coordFrom(pos)
    const cells = wrap2D(this.cellInfos, this.width, this.height)
    const neighbors = this.neighbors8.map(([ox, oy]) => cells(x + ox, y + oy))

    const flagCount = neighbors.filter(cell => cell && cell.state === CellState.FLAGGED).length
    return cells(x, y)!.mineCount === flagCount
  }

  public openAllForGameOver (): ICellInfo[] {
    this.cells.forEach(cell => { cell.state = CellState.OPEN })
    return this.cellInfos
  }

  private openCells (force: boolean, x: number, y: number) {
    const cell = wrap2D(this.cells, this.width, this.height)(x, y)
    const nextCoords = this.neighbors8.map(([ox, oy]) => [x + ox, y + oy] as [number, number])

    if (cell === null) { return }
    if (cell.state === CellState.OPEN) { return }
    if ((!force) && cell.hasMine) { return }

    console.log(`openCells: ${x} ${y}`)
    cell.state = CellState.OPEN
    this.justNowOpened.push(y * this.width + x)
    if (!cell.mineCount) {
      nextCoords.forEach(coord => this.openCells(false, ...coord))
    }
  }

  public openAround (pos: number): WrongCells {
    this.justNowOpened = []
    const [x, y] = this.coordFrom(pos)
    this.openCells(true, x, y)
    const cells = wrap2D(this.cellInfos, this.width, this.height)
    const neighbors = this.neighbors8.map(([ox, oy]) => [x + ox, y + oy])
    neighbors.filter(([x, y]) => {
      const cell = cells(x, y)
      return cell && (cell.state !== CellState.FLAGGED)
    }).forEach(([x, y]) => this.openCells(true, x, y))
    return this.wrongCells(this.justNowOpened)
  }

  private generate (openPos: number): Cell[] {
    const n = this.width * this.height
    const mineNums = mines({ width: this.width, height: this.height })

    const hasMine: boolean[] = new Array(n - 1)
      .fill(true, 0, mineNums)
      .fill(false, mineNums)
    shuffle(hasMine)
    hasMine.splice(openPos, 0, false)
    const hasMine2d = wrap2D(hasMine, this.width, this.height)

    return this.cells.map((dummy, pos) => {
      const [x, y] = this.coordFrom(pos)
      const nextMines = this.neighbors8
        .filter(([ox, oy]) => hasMine2d(x + ox, y + oy))
        .length
      return new Cell(hasMine2d(x, y)!, nextMines, dummy.state)
    })
  }

  toggleState (pos: number): ICellInfo[] {
    const cell = this.cells[pos]
    switch (cell.state) {
      case CellState.CLOSE: cell.state = CellState.FLAGGED; break
      case CellState.FLAGGED: cell.state = CellState.QUESTION_MARKED; break
      case CellState.QUESTION_MARKED: cell.state = CellState.CLOSE; break
      default: break
    }
    return this.cellInfos
  }

  get win (): boolean {
    return this.cells.every(cell =>
      cell.hasMine ? cell.state === CellState.FLAGGED : cell.state === CellState.OPEN
    )
  }

  private wrongCells (openedCells: number[]): WrongCells {
    const wrongCells = openedCells.filter(pos => this.cells[pos].hasMine)
    return wrongCells.length > 0 ? wrongCells : false
  }

  private coordFrom (pos: number): [number, number] {
    return [pos % this.width, Math.floor(pos / this.width)]
  }
}

export {
  IBoardSize
}

export default Board
