








import { Component, Prop, Vue } from 'vue-property-decorator'

@Component({
  name: 'ExplosionAnimation',
  components: {}
})

export default class ExplosionAnimation extends Vue {
  private readonly fps = 30
  private readonly animationSeconds = 1.5
  private tick = 0
  private ctx!: CanvasRenderingContext2D
  private drawTimerHandler!: number

  @Prop() explosions!: [number, number][]

  get body () { return document.body }

  mounted () {
    const canvas = this.$refs.canvas as HTMLCanvasElement
    this.ctx = canvas.getContext('2d')!

    console.log(this.explosions)

    this.drawTimerHandler = setInterval(() => this.draw(), 1000 / this.fps)
  }

  draw () {
    if (this.tick++ > this.fps * this.animationSeconds) {
      clearInterval(this.drawTimerHandler)
      this.$emit('end')
      return
    }

    const r = Math.exp(this.tick / 1.5)
    this.ctx.clearRect(0, 0, this.body.clientWidth, this.body.clientHeight)
    this.ctx.fillStyle = `rgba(255, 0, 0, ${Math.exp(-this.tick / this.fps)}`
    this.explosions.forEach(([cx, cy]) => {
      this.ctx.beginPath()
      this.ctx.ellipse(cx, cy, r, r, 0, 0, 2 * Math.PI)
      this.ctx.fill()
    })
  }
}
